.Main-body {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    height: 100%;
}

.Main-todos-body {
    background-color: forestgreen;
    display: -ms-flexbox;
    display: flex;
    height: 100%;
    width: 100%;
    overflow: auto;
}

.Main-status-container-wrapper {
    -ms-flex: 1 1 auto;
        flex: 1 1 auto;
    height: 100%;
    min-width: 419px;
    background-color: lightblue;

    color: #222222;
}

.Main-status-container-wrapper:not(:last-child) {
    border-right: 2px solid black;
}

.add_todo_block {
    height: 40px;
    background-color:lightgrey;
}

.link-wrapper {
    height: 28px;
    width: 118px;
    border:1px solid black;
    margin: 4px auto;
    background-color:aqua;
    border-radius: 5px;
    -webkit-box-shadow:2px 2px 1px grey;
            box-shadow:2px 2px 1px grey;
    /*color:red;*/
    -webkit-transition: all 0.1s ease-out;
    -o-transition: all 0.1s ease-out;
    transition: all 0.1s ease-out;
}
.link-wrapper:hover{
    -webkit-box-shadow:3px 3px 2px grey;
            box-shadow:3px 3px 2px grey;
    -webkit-filter:brightness(1.3);
            filter:brightness(1.3);
}
.l_col{
    font-size: large;
    font-weight: 600;
    color:red
}
.todos-wrapper{
    min-height: 550px;
}



